.s-login {
  &-wrap {
    display: flex;
    align-items: center;
    padding: 0 20px 0 24px;
    width: 100%;
    height: 70px;
    background: rgba(222, 205, 255, 0.2);
    box-shadow: 0px 1px 7px 0px rgba(30, 30, 41, 0.5);
    border-radius: 10px;
    position: relative;
    backdrop-filter: blur(2px);
    color: #fff;
    z-index: 0;
    margin-top: 15px;

    &:before {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      content: '';
      border-radius: 10px;
      z-index: -1;
      background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.1));
      padding: 1px;
      -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
      mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
      -webkit-mask-composite: xor;
      mask-composite: exclude;
    }
  }

  &-middle {
    margin: 0 12px;
    font-size: 14px;
    font-weight: 600;
    color: #FFFFFF;

    &-no-login {
      flex: 1;
      font-weight: 400;
      color: rgba(255, 255, 255, .5);
    }
  }

  &-img {
    width: 46px;
    height: 36px;
  }

  &-copy {
    width: 12px;
    height: 14px;
    cursor: pointer;
    background: url("../../../../assets/images/menus/file_copy.svg") no-repeat center / contain;
  }

  &-btn {
    height: 29px;
    background: #5A14E1;
    border-radius: 15px;
    border: 1px solid rgba(255, 255, 255, 0.1);
    text-align: center;
    line-height: 27px;
    font-size: 12px;
    font-weight: 600;
    color: #FFFFFF;
    cursor: pointer;
    padding: 0 16px;
  }
}